body { margin :0; padding: 0; list-style:none; font-family: 'NotoSansCJKkr-dl, Sans serif'; font-size: 1rem; }
/*a { text-decoration: none; color: #333;}*/
a { text-decoration: none; color: #333;}
a:hover { text-decoration: underline; }
img { vertical-align: top; }
body { font-family: 'NotoSansCJKkr-dl'; overflow-x: hidden;}



.guide {
	width: 1240px;
	margin: 0 auto;
	position: relative;
}

@media (max-width:1330px) {
	.guide {
		width: 95%;
	}
}

.block {
	display: block;
}

.blind {
	font-size: 0;
	position: absolute;
	top: -999999px;
}

.mid_helper {
	display: inline-block;
	height: 100%;
	vertical-align: middle;
}

.text_cut {
	display: inline-block;
	width: 100%;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}


button {
	border: none;
	background-color: transparent;
}

::-webkit-input-placeholder {
	color: #a3a3a3;
	font-size: 13px;
}
::-moz-placeholder {
	color: #a3a3a3;
	font-size: 13px;
}
:-ms-input-placeholder {
	color: #a3a3a3;
	font-size: 13px;
}
:-moz-placeholder {
	color: #a3a3a3;
	font-size: 13px;
}

/* mobile font resizing */
@media (max-width:380px){
	html {
		font-size:9px;
	}
}

@media (min-width:381px) and (max-width:580px){
	html {
		font-size:10px;
	}
}

@media (min-width:581px) and (max-width:767px){
	html {
		font-size:11.5px;
	}
}

@media (min-width:581px) and (max-width:767px){
	html {
		font-size:11px;
	}
}

@media (min-width:768px) and (max-width:991px){
	html {
		font-size:11.5px;
	}
}

@media (min-width:992px) {
	html {
		font-size: 12px;
	}
}

