@charset "utf-8";

@import "common/normalize";
@import "common/base";
@import "common/common";

body { font-size: 1.167rem }

/* mobile font resizing */
@media (max-width:380px){
	input::placeholder {
		font-size:9px;
	}
	textarea::placeholder {
		font-size:9px;
	}
}

@media (min-width:381px) and (max-width:580px){
	input::placeholder {
		font-size:10px;
	}
	textarea::placeholder {
		font-size:10px;
	}
}

@media (min-width:581px) and (max-width:767px){
	input::placeholder {
		font-size:11.5px;
	}
	textarea::placeholder {
		font-size:11.5px;
	}
}

@media (min-width:581px) and (max-width:767px){
	input::placeholder {
		font-size:11px;
	}
	textarea::placeholder {
		font-size:11px;
	}
}

@media (min-width:768px) and (max-width:991px){
	input::placeholder {
		font-size:11.5px;
	}
	textarea::placeholder {
		font-size:11.5px;
	}
}

@media (min-width:992px) {
	input::placeholder {
		font-size:12px;
	}
	textarea::placeholder {
		font-size:12px;
	}
}





.proposal_wrap {
	max-width: 820px;
	margin: 0 auto;

	section {
		margin-top: 30px;
	}

	table {
		width: 100%;

		caption {
			position: static;
			overflow: hidden;
			width: 1px;
			height: 1px;
			margin: 0;
			padding: 0;
			font-size: 1px;
			text-indent: -9999px;
		}

		td,th {
			height: 45px;
			padding: 10px;
			box-sizing: border-box;
			line-height: 1.6;
			word-break: keep-all;
		}

		th {
			background-color: #eef1f2;
		}

		tbody {
			tr {
				border-bottom: 1px solid #c3c9cd;
			}
		}
	}

	.tbl_st_box {
		border-top: 2px solid #004f9b;

		&.th_tbl {
			border-top: none;

			table {
				border-top: 2px solid #004f9b;
			}

			+ .th_tbl {
				margin-top: 10px;
			}
		}

		&.tbl_scroll {
			overflow-x: auto;
			table {
				min-width: 680px;

				th,td {
					text-align: center;
				}

				th {
					position: relative;

					&:after {
						content: '';
						width: 1px;
						height: 24%;
						background-color: #c3c9cd;
						position: absolute;
						left: 0;
						top: 38%;
					}

					&:first-child {
						&:after {
							display: none;
						}
					}
				}
			}
		}
	}

	.txt_cut {
		display: inline-block;
		width: 100%;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		text-align: left;
	}
}

/* 제안서 타이틀 */
.proposal_title {
	background: rgb(0,161,212);
	background: -moz-linear-gradient(left,  rgba(0,161,212,1) 0%, rgba(41,137,216,1) 61%, rgba(30,87,153,1) 100%, rgba(0,79,155,1) 100%);
	background: -webkit-linear-gradient(left,  rgba(0,161,212,1) 0%,rgba(41,137,216,1) 61%,rgba(30,87,153,1) 100%,rgba(0,79,155,1) 100%);
	background: linear-gradient(to right,  rgba(0,161,212,1) 0%,rgba(41,137,216,1) 61%,rgba(30,87,153,1) 100%,rgba(0,79,155,1) 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00a1d4', endColorstr='#004f9b',GradientType=1 );
	display: table;
	width: 100%;
	position: relative;

	.proposal_tit {
		display: table-cell;
		vertical-align: middle;
		padding: 90px 50px;
		box-sizing: border-box;

		h2 {
			font-size: 2.667rem;
			font-family: 'NotoSansCJKkr-m';
			color: #fff;

			span {
				background-color: #000;
				display: inline-block;
				padding-right: 5px;
			}

			b {
				display: block;
				font-weight: normal;
			}
		}

		img {
			position: absolute;
			bottom: -16px;
			right: 0;
		}
	}
}


.proposer_wrap {
	padding: 20px;
	box-sizing: border-box;

	input[type="text"],
	input[type="tel"],
	input[type="name"] {
		border: 1px solid #c3c9cd;
		height: 43px;
		border-radius: 3px;
		padding-left: 10px;
		box-sizing: border-box;
		width: 100%;

		&:disabled {
			background-color: #e5e7e8;
			color: #666;
		}
	}

	textarea {
		width: 100%;
		resize: none;
		border: 1px solid #c3c9cd;
		border-radius: 3px;
		padding: 10px;
		box-sizing: border-box;
	}

	.pro_tit, .pro_tit_sub {
		margin: 20px 0 10px;
	}

	.pro_tit {
		position: relative;
		padding-left: 10px;
		color: #000;
		font-family: 'NotoSansCJKkr-b';
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		-ms-box-sizing: border-box;
		box-sizing: border-box;

		&:after {
			content: '·';
			position: absolute;
			left: 0;
		}

		&.bgtit {
			background-size: 1.2rem;
			background-repeat: no-repeat;
			background-position: right 2px;
			background-image: url("../images/proposal/pro_tit_bg.png");
			display: inline-block;
			padding-right: 18px;
		}
	}

	.pro_tit_sub {
		font-family: 'NotoSansCJKkr-m';
		color: #333;
	}
}


/* 제안자 정보 */
.proposer_box {
	background-color: #eef1f2;
	padding: 5px 30px 60px;
	box-sizing: border-box;
	margin-top: 0 !important;

	.info_box {
		overflow: hidden;

		li {
			float: left;
			padding-right: 10px;
			box-sizing: border-box;
			width: 23%;
			position: relative;

			&:last-child {
				width: 31%;
				padding-right: 0;
			}

			input {
				height: 43px;
				border-radius: 3px;
			}

			&.search_name {
				position: relative;
				
				input {
					padding-right: 30px;
				}

				button {
					position: absolute;
					right: 16px;
					top: 11px;
					font-size: 20px;
					font-weight: bold;
				}
			}
		}

		+ .info_box {
			margin-top: 5px;
		}
	}
}


.button_box {
	position: relative;
	margin-top: -30px;
	text-align: right;
	padding-bottom: 5px;

	button {
		border-radius: 3px;
	}

	&.add_btn {
		button {
			background-color: #004f9b;
			color: #fff;
			width: 16px;
			height: 16px;
			text-align: center;
			display: inline-block;
			position: relative;
			bottom: -3px;
		}
	}

	&.file_btn {
		margin-bottom: 10px;

		.file_btn_label {
			border: 1px solid #004f9b;
			padding: 3px 10px;
			background-color: #fff;
			color: #0063c3;
			font-size: 12px;

			img {
				vertical-align: middle;
				margin-right: 2px;
				margin-top: -3px;
			}
		}

		input[type="file"] {
		    position: absolute;
		    width: 1px;
		    height: 1px;
		    padding: 0;
		    margin: -1px;
		    overflow: hidden;
		    clip: rect(0, 0, 0, 0);
		    border: 0;
		}
	}

}


.file_box {
	border: 1px dashed #c4cace;
	background-color: #e5e7e8;
	padding: 20px;
	box-sizing: border-box;

	.file_prev {
		padding: 30px 0;
		text-align: center;
		word-break: keep-all;
		color: #666;
	}

	p {
		display: block;

		a {
			display: inline-block;
			background-size: 1rem;
			background-repeat: no-repeat;
			background-position: left 2px;
			background-image: url("../images/proposal/ic_file.png");
			padding-left: 16px;
		}

		button {
			vertical-align: top;
			margin: 5px;
		}
	}

	input[type="text"].form_file {
		display: inline-block;
		width: auto;
		height: auto;
		background-size: 1rem;
		background-repeat: no-repeat;
		background-position: left 2px;
		background-image: url("../images/proposal/ic_file.png");
		padding-left: 16px;
		border: none;

		+ button {
			vertical-align: top;
			margin: 5px;
		}
	}
}


.txt_down_btn {
	background-color: #004f9b;
	width: 100%;
	height: 40px;
	line-height: 40px;
	color: #fff;

	p {
		position: relative;
		width: 95%;
		margin: 0 auto;
		text-align: left;
		background-size: auto;
		background-repeat: no-repeat;
		background-position: center left;
		background-image: url("../images/proposal/ic_write.png");
		padding-left: 25px;
		box-sizing: border-box;

		span {
			position: absolute;
			right: 0;
			top: 40%;
		}
	}
}


.proposal_txt {
	padding: 25px 30px;
	box-sizing: border-box;
	border: 1px solid #c3c9cd;
	line-height: 1.8;
	display: none;

	p {
		color: #666;
		word-break: keep-all;

		&.txt_sub {
			padding-left: 2rem;
			text-indent: -0.7rem;
		}

		b {
			color: #333;
		}
	}
}

/* 제안 상세 */
.pro_detail {
	border: 1px solid #c3c9cd;
	padding: 5px 30px 30px;
	box-sizing: border-box;
}

.check_box {
	overflow: hidden;

	li {
		width: 33.3%;
		float: left;
		padding-right: 2%;
		box-sizing: border-box;
		margin-bottom: 10px;

		&:nth-child(3n) {
			padding-right: 0;
		}
	}

	label {
		width: 100%;
		padding: 15px 5px;
		box-sizing: border-box;
		border: 1px solid #c3c9cd;
		display: inline-block;
		text-align: center;
		border-radius: 3px;
		cursor: pointer;

		span {
			display: block;
			height: 20px;

			i {
				color: #d0d0d0;
			}
		}
	}

	input[type="radio"],
	input[type="checkbox"] {
		width: 0;
		height: 0;
		position: absolute;

		&:checked {
			+ label {
				background-color: #f7fdff;
				border: 1px solid #00a0d3;
				color: #00a0d3;
				font-family: 'NotoSansCJKkr-b';

				span {
					i {
						color: #00a0d3;
					}
				}
			}
		}
	}
}


.btn_wrap {
	text-align: center;
	margin: 20px 0 40px;

	button {
		color: #fff;
		font-family: 'NotoSansCJKkr-m';
		background-color: #004f9b;
		min-width: 100px;
		min-height: 40px;
		border-radius: 3px;
		padding: 5px 10px;
		box-sizing: border-box;
	}
}



/* 팝업 */

.search_info_wrap {
	position: fixed;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	background: rgba(0,0,0,.5);
	display: none;

	tr {
		cursor: pointer;
	}
}

.search_info_tbl {
	display: table;
	width: 100%;
	height: 100%;

	.search_info_cell {
		display: table-cell;
		vertical-align: middle;
		text-align: center;
	}
}

.search_info_box {
	display: inline-block;
	max-width: 500px;
	width: 90%;
    background: #fff;

	.search_info_tit {
		padding: 15px 20px;
		box-sizing: border-box;
		background-color: #0080be;
		color: #fff;
		position: relative;

		p {
			width: 90%;
			text-align: left;
			font-size: 1.333rem;
			line-height: 40px;
		}

		button {
			padding: 10px;
			box-sizing: border-box;
			position: absolute;
			right: 10px;
			top: 1.5rem;

			i {
				font-size: 18px;
			}
		}
	}

	.search_info_content {
		padding: 20px 20px;
		box-sizing: border-box;
		border-top: 1px solid #e5e5e5;
	}

	.search_form {
		position: relative;

		select,input[type='text'] {
			border: 1px solid #c3c9cd;
			height: 43px;
			border-radius: 3px;
		}

		select {
			width: 35%;
		}

		input[type='text'] {
			width: 63%;
			padding: 0 40px 0 10px;
			box-sizing: border-box;
		}

		.search_btn {
			position: absolute;
			right: 10px;
			top: 10px;

			i {
				font-size: 24px;
			}
		}
	}

	.search_info_list {
		padding: 0 20px 20px;
		box-sizing: border-box;
	}
}

.pro_detail_view {
	margin-top: 0px !important;

	.pro_tit {
		color: #004f9b;
		font-size: 1.24rem;
	}

	.file_box {
		border: none;
		background: none;
		padding: 0 0 0 10px;

		p {
			margin-bottom: 5px;
		}
	}

	.pro_search {
		border: 1px solid #c3c9cd;
		background-color: #eef1f2;
		padding: 5px 20px 10px;
		margin-bottom: 20px;

		select,input[type='text'] {
			width: 45%;
			height: 38px;
			border: 1px solid #c3c9cd;
			border-radius: 3px;
			padding: 0 10px;
			box-sizing: border-box;
		}

		.search_sel {
			overflow: hidden;
			
			> div {
				float: left;
				width: 50%;
			}
		}

		.search_sel, .search_txt {
			margin-top: 5px;
		}

		.search_date {
			.ui-datepicker-trigger {
				position: relative;
				right: 20px;
				top: 13px;
			}

			> span {
				display: inline-block;
			}

			input {
				background-size: auto;
				background-repeat: no-repeat;
				background-position: 95% center;
				background-image: url("../images/proposal/calendar.png");
				width: 46%;
			}
		}

		.search_option {
			select {
				width: 48%;

				&:nth-child(2) {
					margin-right: 2%;
				}
			}
		}

		.search_txt {
			position: relative;

			select {
				width: 23%;
			}
			input[type="text"] {
				width: 76%;
			}

			.search_btn {
				position: absolute;
				right: 3px;
				top: 10px;
				height: 100%;
				top: 0;
				padding: 10px;
				box-sizing: border-box;
			}
		}
	}
}

.ui-datepicker select.ui-datepicker-month, .ui-datepicker select.ui-datepicker-year {
	vertical-align: top;
}




































@media (max-width: 730px) {

	.proposal_title {
		.proposal_tit {
			padding: 50px 40px;

			h2 {
				font-size: 2.2rem;
			}

			img {
				width: 40%;
			}
		}
	}

	.proposer_box {
		.info_box {
			li {
				width: 33.3%;

				&:nth-child(3) {
