/*****************************/
/*******    Paging    ********/
/*****************************/


@media (min-width: 768px) {
	.paging_wrap {
		.pageview {
			a {
				&:hover {
					text-decoration: none;
					color: #004f9b;

					&:after {
						width: 100%;
					}
				}
			}
		}
	}
}


.paging_wrap {
	margin: 30px auto 0;
	text-align: center;

	.pageview {
		display: inline-block;

		a {
			display: block;
			float: left;
			color: #444;
			text-align: center;
			transition: all .3s;
			padding: 0 10px;
			line-height: 30px;
			position: relative;
			margin: 0 3px;

			&:after {
				content: '';
				width: 0;
				height: 1px;
				position: absolute;
				bottom: 0;
				left: 0;
				right: 0;
				margin: 0 auto;
				background-color: #004f9b;
				transition: all .3s;
			}

			&.active {
				color: #004f9b;

				&:after {
					width: 100%;
				}
			}

			&.arrow {
				border: 1px solid #bfbfbf;
				width: 30px;
				height: 30px;
				line-height: 30px;
				margin: 0 4px;
				background-size: auto;
				background-repeat: no-repeat;
				background-position: center;
				padding: 0;

				&:after {
					display: none;
				}

				&:hover {
					&:after {
						display: none;
					}
				}
			}

			&.dub {
				background-image: url(../images/proposal/ic_dub_arrow.png);
			}

			&.next {
				transform: rotate(180deg);
			}

			&.sing {
				background-image: url(../images/proposal/ic_arrow.png);
				margin-right: 25px;

				&.next {
					margin-right: 0;
					margin-left: 25px;
				}
			}

		}
	}
}


@media (max-width: 580px) {

	.paging_wrap {
		.pageview {
			a {
				padding: 0 5px;
				line-height: 25px;
				margin: 0 5px;

				&.arrow {
					width: 25px;
					height: 25px;
				}

				&.sing {
					margin-right: 15px;

					&.next {
						margin-left: 15px;
					}
				}
			}
		}
	}
}

@media (max-width: 380px) {

	.paging_wrap {
		.pageview {
			a {
				&.arrow {
					width: 20px;
				}

				&.sing {
					margin-right: 10px;

					&.next {
						margin-left: 10px;
					}
				}
			}
		}
	}
}




.btn_wrap {
	text-align: right;

	a {
		display: inline-block;
		padding: 8px 25px;
		background-color: #004f9b;
		color: #fff;
	}
}


